import React, { useContext, useEffect } from 'react';
import { Context } from '../..';
import InputErrFoot from './components/InputErrFoot';
import { change, focus, renderInput } from './components/inputFocus';
import { valid } from './components/validate';
import { InputFiledTypes } from './Input.config';
import './style/Input.scss';

export default function OtherInpurt({
  type,
  register,
  name,
  errors,
  setValue,
  textFiled,
}: InputFiledTypes) {
  const err: string = errors[name] && (errors[name]?.message || 'Error');
  const { basket } = useContext(Context);

  const changeSale = (e) => {
    if (basket.getSale > 0) setValue(name, 'aaa');
    else change(e, setValue, name);
  };

  useEffect(() => {
    renderInput();
  }, []);

  return (
    <div className="inputFiled">
      <div
        className={
          basket.setSale > 0
            ? 'inputFiled__block sale '
            : 'inputFiled__block old all'
        }
      >
        <div className="inputFiled__text old all">
          <p className="mini">{textFiled}</p>
        </div>
        <input
          {...register(name, {
            required: 'Field must be filled',
            pattern: valid(type, name),
          })}
          onChange={(e) => changeSale(e)}
          onFocus={focus}
          onBlur={focus}
          type={type}
        />
      </div>
      {/* {err && <InputErrFoot err={err} />} */}
    </div>
  );
}

export function AllInpurt({
  type,
  register,
  name,
  setValue,
  textFiled,
}: InputFiledTypes) {
  useEffect(() => {
    renderInput();
  }, []);

  return (
    <div className="inputFiled">
      <div className={'inputFiled__block old all'}>
        <div className="inputFiled__text old all">
          <p className="mini">{textFiled}</p>
        </div>
        <input
          {...register(name, {
            required: 'Field must be filled',
            pattern: valid(type, name),
          })}
          onChange={(e) => change(e, setValue, name)}
          onFocus={focus}
          onBlur={focus}
          type={type}
        />
      </div>
    </div>
  );
}
