import { ProductBasket } from '../ProductBasket/ProductBasket';
import { observer } from 'mobx-react-lite';
import { useContext } from 'react';
import { Context } from '../../../..';
import { BasketDeviceConfig } from '../../../../mobx/GlobalState.config';

export const BasketBodyOne: React.FC = observer(() => {
  const { basket } = useContext(Context);

  return (
    <div className="productbasket__goods">
      {basket.getProducts.map((item: BasketDeviceConfig) => {
        return <ProductBasket product={item} key={item.id} />;
      })}
    </div>
  );
});
