import './style/ProductBasket.scss';
import React, { useState, useEffect, useContext } from 'react';
import { ReactComponent as Heart } from '../../../../img/icons/heart.svg';
import { ReactComponent as X } from '../../../../img/icons/x.svg';
import { observer } from 'mobx-react-lite';
import { useNavigate } from 'react-router-dom';
import { useFetching } from '../../../../http/shopAPI';
import useLoading from '../../../../Hooks/useLoading';
import { DeviceConfigAll } from '../../../../mobx/GlobalState.config';
import { API_URL } from '../../../../components/Routers/links';
import { TypeGoods } from '../BasketPieces.conf';
import { remove } from '../../../../http/basketAPI';
import { Context } from '../../../..';
import { BtnBasket, btnHOC } from '../../../../UI/ProductBtn/ProductBtn';
import HeartBtn from '../../../../UI/HeartBtn/HeartBtn';

export const ProductBasket = observer(({ product }: TypeGoods) => {
  const { basket } = useContext(Context);
  const router = useNavigate();
  const [item, setItem] = useState<DeviceConfigAll | null>(null!);
  const [, fetching] = useLoading(async () => {
    const response = await useFetching.getOne(product.id);
    setItem(response);
  });
  const Btn = btnHOC(BtnBasket);
  useEffect(() => {
    fetching();
  }, []);

  const clean = async () => {
    const arrowgo = document.querySelector('.basket__price') as HTMLElement;
    const response = await remove(product.id);
    basket.setProducts = response.devices;
    response.devices.length < 1 && router('/shop');

    if (response.devices.length == 1) {
      console.log(1);

      arrowgo?.classList.remove('bottom');
      arrowgo?.classList.remove('show');
      arrowgo?.classList.remove('showtwo');
    }
  };

  const route = () => {
    router(`/basket/${product.id}`);
    document.documentElement.scrollIntoView(true);
  };

  return (
    <div className="productbasket__item">
      <div className="productbasket__first">
        <div className="productbasket__blockimg" onClick={route}>
          <div
            className="productbasket__img "
            style={{
              backgroundImage: `url(${API_URL}${
                Array.from(
                  Object.values(JSON.parse(item?.deviceimg.img || '{}'))
                )[0]
              })`,
            }}
          ></div>
        </div>
      </div>

      <div className="productbasket__second">
        <div className="productbasket__text">
          <div className="productbasket__name">
            <p className="basket">
              {item && item?.name.length <= 20
                ? item?.name
                : item?.name
                    .split('')
                    .filter((item, index) => index <= 20)
                    .join('') + '...'}
            </p>
          </div>
          <div className="productbasket__category">
            <p className="mini basket">{item?.brand.name}</p>
          </div>
        </div>
        <div className="productbasket__btnfirst">
          <Btn product={product} />
        </div>
      </div>
      <div className="productbasket__btnsecond">
        <Btn product={product} />
      </div>
      <div className="productbasket__midst">
        <div className="productbasket__price">
          {basket.getSale != 0 && (
            <p className="big sale">{`$${
              +product?.price * product.quantity -
              ((+product?.price * product.quantity) / 100) *
                (basket.getSale + +product?.sale)
            }`}</p>
          )}
          {product.sale > 0 && basket.getSale <= 0 ? (
            <>
              <p className={'basket newcols big'}>{`$${
                +product?.price * product?.quantity -
                ((+product?.price * product?.quantity) / 100) * product.sale
              }`}</p>
              <p className={'basket through mini'}>{`$${
                +product?.price * product?.quantity
              }`}</p>
            </>
          ) : (
            <p
              className={basket.getSale != 0 ? 'mini old basket' : 'big basket'}
            >{`$${+product?.price * product?.quantity}`}</p>
          )}
        </div>
      </div>

      <div className="productbasket__end">
        <HeartBtn id={product.id} />
        <div onClick={clean} className="productbasket__delete">
          <X />
        </div>
      </div>
    </div>
  );
});
