import { observer } from 'mobx-react-lite';
import { useContext, useEffect } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { Context } from '../../../..';
import { Button } from '../../../../UI/Button/Button';
import OtherInpurt from '../../../../UI/Input/OtherInput';
import './style/SaleBlock.scss';

const SaleBlock: React.FC = observer(() => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    setValue,
  } = useForm({ mode: 'onChange' });
  const { basket } = useContext(Context);
  const submit: SubmitHandler<FieldValues> = () => {
    if (basket.getSale > 0) {
      basket.setSale = 0;
      setValue('code', '');
    } else {
      basket.setSale = 10;
    }
  };

  useEffect(() => {
    if (basket.getSale > 0) {
      setValue('code', 'abcAD55');
    }
  }, []);

  return (
    <form onSubmit={handleSubmit(submit)} className="setsale">
      <div className="setsale__input">
        <OtherInpurt
          register={register}
          type="text"
          setValue={setValue}
          errors={errors}
          name="code"
          textFiled="Promocode"
        />
      </div>
      <div className="setsale__btn">
        <Button name="sale">
          <p className="mini">{basket?.getSale != 0 ? 'delete' : 'apply'}</p>
        </Button>
      </div>
    </form>
  );
});

export default SaleBlock;
