import './style/Basket.scss';
import { useEffect } from 'react';
import { ReactComponent as Cart } from '../../img/icons/cart.svg';
import { BasketBodyOne } from './components/BasketBodyOne/BasketBodyOne';
import { useContext } from 'react';
import { Context } from '../..';
import { observer } from 'mobx-react-lite';
import SaleBlock from './components/SaleBlock/SaleBlock';
import { TotalPrice } from './components/TotalPrice/TotalPrice';
import { useNavigate } from 'react-router-dom';
import { clear } from '../../http/basketAPI';
import { Button } from '../../UI/Button/Button';

const Basket = observer(() => {
  const { basket } = useContext(Context);
  const router = useNavigate();

  useEffect(() => {
    document.title = `Basket`;
  }, []);

  const clean = async () => {
    await clear();
    basket.clear();
    router('/shop');
  };

  return (
    <div className="basket  indent_last">
      <div className="container">
        <div className="basket__row">
          <div className="pageonebas__header">
            <div className="pageonebas__logo">
              <div className="pageonebas__img">
                <Cart />
              </div>
              <div className="pageonebas__title">
                <h3>Basket</h3>
              </div>
            </div>
            <div className="pageonebas__clear">
              <p onClick={clean} className="mini">
                Clear for basket
              </p>
            </div>
          </div>
          <div className="pageonebas">
            <BasketBodyOne />

            <div className="basket__aside scroll" data-sale>
              <div className="basket__price">
                <SaleBlock />

                <TotalPrice />
                <div className="pageonebas__btn">
                  <Button name="sign">
                    <p className="mini">make an order</p>
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});

export default Basket;
